.page-wrapper {
	display: flex;
	flex-direction: column;
	padding: 1rem 0.7rem;
}

.page-wrapper .nav {
	display: flex;
	justify-content: center;
	padding: 0 5%;
}

.page-wrapper .nav .logo {
	cursor: pointer;
	width: 140px;
	height: auto;
}

.image-wrapper .hero {
	display: none;
	max-width: 100%;
	height: auto;
}

.form-wrapper {
	display: flex;
	flex-direction: column;
	padding: 1rem;
}

.form-wrapper>h1 {
	font-size: calc(var(--fs-h2) / 2);
	font-weight: 500;
	margin-bottom: 2rem;
}

