.form-wrapper input:focus,
.form-wrapper select:focus {
	border: 2px solid var(--bg-btn);
	outline: none;
}

.form-wrapper div>div:has(button) {
	width: 100%;
}

.form-wrapper button {
	background-color: var(--bg-btn);
	border: 2px transparent;
	border-radius: 10px;
	color: var(--white);
	cursor: pointer;
	font-family: inherit;
	font-size: inherit;
	font-weight: inherit;
	padding: 0.79rem 1rem;
	text-transform: capitalize;
	width: 100%;
}

.form-wrapper button.redBtn {
	background-color: red;
}

.form-wrapper button.green {
	background-color: rgb(0, 255, 128);
}

.form-wrapper button.btn-disabled {
	background-color: var(--loading);
}

.form-wrapper .link-wrapper {
	align-self: center;
	margin-top: 0.75rem;
}

/* tablet screen */
@media screen and (min-width: 768px) {
	.page-wrapper {
		height: 100vh;
		padding: 1rem;
	}

	.page-wrapper .nav {
		align-self: flex-start;
	}

	.page-wrapper .nav .logo {
		width: 70px;
	}

	.page-wrapper .main {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.main .image-wrapper .hero {
		display: unset;
		object-fit: none;
		width: 400px;
	}

	.main .form-wrapper {
		width: 40%;
	}
}

/* desktop screen 1024px */
@media screen and (min-width: 1024px) {
	.page-wrapper {
		height: unset;
		padding: 1rem 1rem 2rem;
	}

	.page-wrapper .main {
		justify-content: unset;
		padding: 0 3rem;
	}

	.page-wrapper .main .image-wrapper {
		width: 50%;
	}

	.page-wrapper .main .image-wrapper .hero {
		width: 500px;
	}

	.page-wrapper .main .form-wrapper {
		width: 30%;
	}

	.form-wrapper>h1 {
		font-size: var(--fs-h3);
	}

	.form-wrapper label,
	.form-wrapper input,
	.form-wrapper select,
	.form-wrapper .link-wrapper {
		/* font-size: var(--fs-body2); */
		font-size: calc(var(--fs-h4) / 2);
	}

	.form-wrapper button {
		font-size: var(--fs-body2);
	}
}

/* desktop screen 1440px */
@media screen and (min-width: 1440px) {
	.page-wrapper {
		gap: 10rem;
	}

	.page-wrapper .main {
		padding: 0 10rem;
	}
}

/* desktop screen 1440px */
@media screen and (min-width: 2560px) {
	.page-wrapper {
		gap: 30rem;
	}

	.page-wrapper .main {
		padding: 0 40rem;
	}
}